main {
  padding-bottom: 60px;
  position: relative;
}

#book-list.hide {
  display: none;
}

.book-card {
  background: #fff;
  border: 1px solid #ddd;
  border-top-right-radius: 30px;
  border-bottom-left-radius: 30px;
  margin: 10px 0;
  display: flex;
  position: relative;
}

.book-img img {
  border: 1px solid #ddd;
  border-bottom-left-radius: 30px;
  width: 130px;
  height: 100%;
}

.book-info {
  flex: 1;
  padding: 10px;
}

.book-info:first-child {
  padding-bottom: 10px;
}

.book-rating {
  color: #fff;
  background-color: #7f6639;
  border-top-left-radius: 20px;
  padding: 2px 5px;
  position: absolute;
  bottom: 0;
  right: 0;
}

#book-form {
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 10px;
  margin-top: 20px;
  padding: 20px;
  display: none;
}

#book-form.show {
  display: block;
}

#book-form h2 {
  margin-bottom: 10px;
}

#book-form div {
  margin-bottom: 15px;
}

#book-form div:last-child {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 10px;
  display: flex;
}

#book-form label {
  margin-bottom: 5px;
  display: block;
}

#book-form input, #book-form select {
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 100%;
  padding: 8px;
}

#book-form button {
  color: #fff;
  cursor: pointer;
  background-color: #7f6639;
  border: none;
  border-radius: 5px;
  flex: 1;
  min-width: 0;
  padding: 10px 20px;
}

#book-form button:hover, #sign-in:hover {
  background-color: #6b5430;
}

#delete-book {
  background-color: #d9534f !important;
}

#delete-book:hover {
  background-color: #c9302c !important;
}

.highlight {
  background-color: #e5eee2;
  box-shadow: 0 8px 16px #0006;
}

.sing-in-container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

#sign-in {
  color: #fff;
  cursor: pointer;
  background-color: #7f6639;
  border: none;
  border-radius: 5px;
  min-width: 0;
  padding: 10px 20px;
}

#chatbot-container {
  background: #fff;
  border: 1px solid #ccc;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 40px;
  right: 20px;
  box-shadow: 0 4px 6px #0000001a;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 200px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  width: calc(100% - 60px);
  margin-right: 5px;
  padding: 5px;
}

#send-btn {
  cursor: pointer;
  padding: 5px 10px;
}

.history {
  padding-bottom: 15px;
}
